import { Component, OnInit } from '@angular/core';
import { KalenderCutiPengajuanService, KalenderCutiPengaturanService, DirektoriPegawaiMasterService, DirektoriPegawaiPosisiService, KalenderCutiPengajuanTanggalService, DirektoriVPegawaiPosisiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Cuti_pengajuan, Pegawai_master, Cuti_pengajuan_tanggal, Cuti_pengaturan, V_pegawai_posisi } from 'src/app/sdk/hr/models';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private kalenderCutiPengajuanService: KalenderCutiPengajuanService,
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private kalenderCutiPengajuanTanggalService: KalenderCutiPengajuanTanggalService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) { }


    id_cuti_pengajuan: string = ''
    dataPengajuan: Cuti_pengajuan = {}
    dataPegawai: Pegawai_master = {}
    dataTanggalPengajuan: Cuti_pengajuan_tanggal[] = []
    dataCutiPengaturan: Cuti_pengaturan = {}
    dataPegawaiPosisi: V_pegawai_posisi = {}
    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
        })

        this.activatedRoute.paramMap.subscribe(
            params => {
                console.log(params.get('id_cuti_pengajuan'))
                if (params.get('id_cuti_pengajuan')) {
                    this.id_cuti_pengajuan = params.get('id_cuti_pengajuan')
                }

            }
        )

        this.getCutiPengajuan()
        this.getTanggalPengajuan()

    }

    getCutiPengajuan() {
        this.kalenderCutiPengajuanService.getKalenderCutiPengajuansId(this.id_cuti_pengajuan).subscribe(
            res => {
                this.dataPengajuan = res.data
                this.getPegawai(res.data.id_pegawai, 'dataPegawai')
                this.getPegawai(res.data.id_pegawai_atasan, 'pegawai_atasan')
                this.getPegawai(res.data.id_pegawai_close, 'pegawai_close')
                this.getPegawaiPosisi(res.data.id_pegawai)
                this.getPengaturan(res.data.id_cuti_pengaturan)


            }
        )
    }

    getPegawai(id_pegawai: string, type) {
        if (id_pegawai) {
            this.direktoriPegawaiMasterService.getDirektoriPegawaiMastersId(id_pegawai).subscribe(
                res => {
                    if (type == 'dataPegawai') {
                        this.dataPegawai = res.data
                    }
                    if (type == 'pegawai_atasan') {
                        this.pegawai_atasan = res.data.nama_depan + ' ' + res.data.nama_belakang
                    }
                    if (type == 'pegawai_close') {
                        this.pegawai_close = res.data.nama_depan + ' ' + res.data.nama_belakang
                    }

                }
            )
        }

    }

    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            res => {
                this.dataPegawaiPosisi = res.data
            }
        )
    }

    getPengaturan(id_cuti_pengaturan: string) {
        this.kalenderCutiPengaturanService.getKalenderCutiPengaturansId(id_cuti_pengaturan).subscribe(
            res => {
                this.dataCutiPengaturan = res.data
            }
        )
    }


    getTanggalPengajuan() {
        this.kalenderCutiPengajuanTanggalService.getKalenderCutiPengajuanTanggals(JSON.stringify({ id_cuti_pengajuan: this.id_cuti_pengajuan })).subscribe(
            res => {
                this.dataTanggalPengajuan = res.data
            }
        )
    }


    // Approval
    is_approved = false;
    prosess_apprved() {
        this.is_approved = !this.is_approved
    }

    pegawai_atasan = ""
    pegawai_close = ""

    approved() {
        this.spinner.show()

        if (this.dataPengajuan.status_cuti == 'approved') {
            this.dataPengajuan.data_approval_close = null;
        }
        this.kalenderCutiPengajuanService.putKalenderCutiPengajuansId({ id: this.id_cuti_pengajuan, body: this.dataPengajuan }).subscribe(
            data => {
                this.spinner.hide()
                this.getCutiPengajuan()
                this.is_approved = !this.is_approved;
            }
        )
    }

    approvedtanggal(id_cuti_pengajuan_tanggal, item) {
        this.spinner.show()
        this.kalenderCutiPengajuanTanggalService.putKalenderCutiPengajuanTanggalsId({ id: id_cuti_pengajuan_tanggal, body: item }).subscribe(
            data => {
                this.spinner.hide()
                this.getTanggalPengajuan()
            }
        )
    }
    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'cuti', 'list'])
    }
}

