import { PegawaiDetailService } from './../../service/pegawai-detail.service';
import { Cuti_pengajuan_tanggal } from 'src/app/sdk/hr/models/cuti-_pengajuan-_tanggal';
import { Cuti_pengajuan } from 'src/app/sdk/hr/models/cuti-_pengajuan';
import { Component, OnInit } from '@angular/core';
import { KalenderCutiPengajuanService, KalenderCutiPengaturanService, DirektoriPegawaiMasterService, KalenderCutiPengajuanTanggalService, KalenderCutiPengaturanPerpegawaiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Cuti_pengaturan, Pegawai_master, Cuti_pengaturan_perpegawai } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private kalenderCutiPengajuanService: KalenderCutiPengajuanService,
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private kalenderCutiPengaturanPerpegawaiService: KalenderCutiPengaturanPerpegawaiService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private kalenderCutiPengajuanTanggalService: KalenderCutiPengajuanTanggalService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pegawaiDetailService: PegawaiDetailService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,

    ) { }

    id_pegawai: string = ''
    id_cuti_pengajuan: string = ''
    pengajuan: Cuti_pengajuan = {}
    dataTanggal: Cuti_pengajuan_tanggal[] = []
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
            this.getPreeData()
            this.form()
            this.beforeCreate()
        })
    }

    limit = 0
    getLimit() {
        let param = {
            action: 'ceksaldo',
            id_cuti_pengaturan: this.dataForm.get('id_cuti_pengaturan').value,
            id_pegawai: this.id_pegawai
        }
        this.kalenderCutiPengajuanService.getKalenderCutiPengajuans(JSON.stringify(param)).subscribe(
            data => {
                this.limit = data.data['limit']
            }
        )
    }

    listCutiPengaturan: Cuti_pengaturan[] = []
    getPreeData() {
        this.kalenderCutiPengaturanService.getKalenderCutiPengaturans(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(data => {
            this.listCutiPengaturan = data.data
        })
    }

    beforeCreate() {
        this.kalenderCutiPengajuanService.postKalenderCutiPengajuans(this.pengajuan).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.id_cuti_pengajuan = data.data.id_cuti_pengajuan
                console.log(data.data.id_cuti_pengajuan)
                this.getTanggal()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di load data')
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    tanggal_cuti: string = ''
    tambahTanggal() {
        if (this.tanggal_cuti == '' || this.tanggal_cuti == null) {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
            )
            return false
        }

        this.kalenderCutiPengajuanTanggalService.postKalenderCutiPengajuanTanggals({
            id_cuti_pengajuan: this.id_cuti_pengajuan,
            tanggal_cuti: this.tanggal_cuti
        }).subscribe(res => {
            this.getTanggal()
        })
    }


    listTanggal: any[] = []
    getTanggal() {
        let filter = {
            id_cuti_pengajuan: this.id_cuti_pengajuan,
        }
        this.kalenderCutiPengajuanTanggalService.getKalenderCutiPengajuanTanggals(JSON.stringify(filter)).subscribe(
            res => {
                this.listTanggal = res.data
            })
    }
    deleteTanggal(id) {
        this.spinner.show()
        this.kalenderCutiPengajuanTanggalService.deleteKalenderCutiPengajuanTanggalsId(id).subscribe(data => {
            this.getTanggal()
            this.spinner.hide()
        })
    }


    dataForm: FormGroupTypeSafe<Cuti_pengajuan>
    form() {
        this.dataForm = this.fb.group<Cuti_pengajuan>({
            keterangan_cuti: new FormControl(''),
            id_cuti_pengaturan: new FormControl('', [Validators.required]),
            no_yg_bisa_dihubungi: new FormControl(''),
            status_cuti: new FormControl(''),
            alasan_approval: new FormControl(''),
            tanggal_approval: new FormControl(''),
            validasi: new FormControl(''),
            id_cuti_pengajuan: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
            id_dokumen: new FormControl(''),
            id_pegawai_atasan: new FormControl(''),
            date_approval_atasan: new FormControl(''),
            id_pegawai_close: new FormControl(''),
            data_approval_close: new FormControl(''),
            id_user: new FormControl('')
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataForm.patchValue({ id_pegawai: this.id_pegawai });

        this.pengajuan = this.dataForm.value
        this.kalenderCutiPengajuanService
            .putKalenderCutiPengajuansIdResponse({
                id: this.id_cuti_pengajuan,
                body: this.pengajuan,
            })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'cuti', 'list'])
    }

}
