import { Cuti_pengajuan } from 'src/app/sdk/hr/models/cuti-_pengajuan';
import { Component, OnInit } from '@angular/core';
import { KalenderCutiPengajuanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private kalenderCutiPengajuanService: KalenderCutiPengajuanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    dataPengajuanCuti: Cuti_pengajuan[] = []
    id_cuti_pengajuan: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.getPengajuan()
    }


    getPengajuan() {
        this.kalenderCutiPengajuanService.getKalenderCutiPengajuans(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataPengajuanCuti = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.kalenderCutiPengajuanService
            .deleteKalenderCutiPengajuansId(id)
            .subscribe(data => {
                this.getPengajuan()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
