import { filter } from 'rxjs/operators';
import { Cuti_pengajuan_tanggal } from 'src/app/sdk/hr/models/cuti-_pengajuan-_tanggal'
import { Component, OnInit } from '@angular/core'
import {
    KalenderCutiPengajuanService,
    KalenderCutiPengaturanService,
    DirektoriPegawaiMasterService,
} from 'src/app/sdk/hr/services'
import { KalenderCutiPengajuanTanggalService } from 'src/app/sdk/hr/services/kalender-cuti-pengajuan-tanggal.service'
import { Cuti_pengajuan, Pegawai_master, Cuti_pengaturan } from 'src/app/sdk/hr/models'
import { NgxSpinnerService } from 'ngx-spinner'
import { NzNotificationService } from 'ng-zorro-antd'
import {
    FormBuilderTypeSafe,
    FormGroupTypeSafe,
} from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { Router } from '@angular/router'
import { FormControl, Validators } from '@angular/forms'

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    constructor(
        private kalenderCutiPengajuanService: KalenderCutiPengajuanService,
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private kalenderCutiPengajuanTanggalService: KalenderCutiPengajuanTanggalService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
    ) { }

    dataPengajuan: Cuti_pengajuan = {}
    dataTanggal: Cuti_pengajuan_tanggal[] = []
    id_cuti_pengajuan: string = ''

    ngOnInit() {
        this.form()
        this.getPreeData()
        this.beforeCreate()

        //anggota
    }
    //-------------------------------------

    listCutiPengaturan: Cuti_pengaturan[] = []
    listPegawai: Pegawai_master[] = []
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })

        this.kalenderCutiPengaturanService.getKalenderCutiPengaturans().subscribe(data => {
            this.listCutiPengaturan = data.data
        })
    }

    //----------Tanggal-----------------

    tanggal_cuti: string = ''
    tambahTanggal() {
        if (this.tanggal_cuti == '' || this.tanggal_cuti == null) {
            this.notification.blank(
                'Notification Title',
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
            )
            return false
        }

        this.kalenderCutiPengajuanTanggalService.postKalenderCutiPengajuanTanggals({
            id_cuti_pengajuan: this.id_cuti_pengajuan,
            tanggal_cuti: this.tanggal_cuti
        }).subscribe(res => {
            this.getTanggal()
        })
    }

    listTanggal: any[] = []
    getTanggal() {
        let filter = {
            id_cuti_pengajuan: this.id_cuti_pengajuan,
        }
        this.kalenderCutiPengajuanTanggalService.getKalenderCutiPengajuanTanggals(JSON.stringify(filter)).subscribe(
            res => {
                this.listTanggal = res.data
            })
    }
    deleteTanggal(id) {
        this.spinner.show()
        this.kalenderCutiPengajuanTanggalService.deleteKalenderCutiPengajuanTanggalsId(id).subscribe(data => {
            this.getTanggal()
            this.spinner.hide()
        })
    }

    //----------Tanggal-----------------
    beforeCreate() {
        this.kalenderCutiPengajuanService.postKalenderCutiPengajuans(this.dataPengajuan).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.id_cuti_pengajuan = data.data.id_cuti_pengajuan
                console.log(data.data.id_cuti_pengajuan)
                this.getTanggal()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di load data')
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    dataForm: FormGroupTypeSafe<Cuti_pengajuan>
    form() {
        this.dataForm = this.fb.group<Cuti_pengajuan>({
            id_pegawai: new FormControl('', [Validators.required]),
            keterangan_cuti: new FormControl(''),
            id_cuti_pengaturan: new FormControl('', [Validators.required]),
            no_yg_bisa_dihubungi: new FormControl('', [Validators.required]),
            status_cuti: new FormControl(''),
            alasan_approval: new FormControl(''),
            tanggal_approval: new FormControl(''),
            validasi: new FormControl(''),
            id_cuti_pengajuan: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),

        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPengajuan = this.dataForm.value
        this.kalenderCutiPengajuanService
            .putKalenderCutiPengajuansIdResponse({
                id: this.id_cuti_pengajuan,
                body: this.dataPengajuan,
            })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/kalender/cuti/list'])
    }
}
