import { Component, OnInit } from '@angular/core';
import { KalenderCutiPengajuanService, KalenderCutiPengaturanService, DirektoriPegawaiMasterService, KalenderCutiPengajuanTanggalService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Cuti_pengajuan, Pegawai_master, Cuti_pengaturan, Cuti_pengajuan_tanggal } from 'src/app/sdk/hr/models';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private kalenderCutiPengajuanService: KalenderCutiPengajuanService,
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private kalenderCutiPengajuanTanggalService: KalenderCutiPengajuanTanggalService,
        private pegawaiDetailService: PegawaiDetailService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) { }

    id_cuti_pengajuan: string = ''
    dataPengajuan: Cuti_pengajuan = {}
    id_pegawai = ''


    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
        })
        this.activatedRoute.paramMap.subscribe(data => {
            if (data.get('id_cuti_pengajuan')) {
                this.id_cuti_pengajuan = data.get('id_cuti_pengajuan')
                console.log(data.get('id_cuti_pengajuan'))

                this.getPreeData()
                this.form()
                this.getData()
                this.getTanggal()
            }
        })
    }

    listCutiPengaturan: Cuti_pengaturan[] = []
    getPreeData() {
        this.kalenderCutiPengaturanService.getKalenderCutiPengaturans(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(data => {
            this.listCutiPengaturan = data.data
        })
    }

    tanggal_cuti: string = ''
    tambahTanggal() {
        if (this.tanggal_cuti == '' || this.tanggal_cuti == null) {
            this.notification.blank(
                'Notification Title',
                // tslint:disable-next-line: max-line-length
                'This is the content of the notification. This is the content of the notification. This is the content of the notification.',
            )
            return false
        }
        this.kalenderCutiPengajuanTanggalService.postKalenderCutiPengajuanTanggals({ tanggal_cuti: this.tanggal_cuti, id_cuti_pengajuan: this.id_cuti_pengajuan, }).subscribe(
            res => {
                this.getTanggal()
            }
        )
    }

    listTanggal: Cuti_pengajuan_tanggal[] = []
    getTanggal() {
        let filter = {
            id_cuti_pengajuan: this.id_cuti_pengajuan,
        }
        this.kalenderCutiPengajuanTanggalService.getKalenderCutiPengajuanTanggals(JSON.stringify(filter)).subscribe(
            res => {
                this.listTanggal = res.data
            })
    }

    deleteTanggal(id) {
        this.spinner.show()
        this.kalenderCutiPengajuanTanggalService
            .deleteKalenderCutiPengajuanTanggalsId(id)
            .subscribe(data => {
                this.getTanggal()
                this.spinner.hide()
            })
    }

    getData() {
        this.kalenderCutiPengajuanService.getKalenderCutiPengajuansId(this.id_cuti_pengajuan).subscribe(
            data => {
                this.dataForm.setValue(data.data)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Cuti_pengajuan>
    form() {
        this.dataForm = this.fb.group<Cuti_pengajuan>({
            id_pegawai: new FormControl('', [Validators.required]),
            keterangan_cuti: new FormControl(''),
            id_cuti_pengaturan: new FormControl(''),
            no_yg_bisa_dihubungi: new FormControl(''),
            status_cuti: new FormControl(''),
            alasan_approval: new FormControl(''),
            tanggal_approval: new FormControl(''),
            validasi: new FormControl(''),
            id_cuti_pengajuan: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_user: new FormControl(''),
            // id_data_flow: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPengajuan = this.dataForm.value
        this.kalenderCutiPengajuanService
            .putKalenderCutiPengajuansIdResponse({
                id: this.id_cuti_pengajuan,
                body: this.dataPengajuan,
            })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'cuti', 'list'])
    }


}
